body{
    height: 400px;
    overflow: hidden;
}
.grid-container {
    min-width: 1200px;
    height: 600px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding-top: 20px;
}

.grid-container .checkbox-wrapper {
    border-radius: 25px;

}

.grid-container .checkbox-wrapper div {
    border: none;
    margin: 20px;
    display: grid;
    grid-template-columns: 340px 520px 270px;
}
.grid-container div div span{
    font-size: 20px;
    color: black;
    font-weight: 500;
}
.price_block{
    position: fixed;
    display: flex;
    width: 600px;
    height: 50px;
    justify-content: space-evenly;
    padding-top: 20px;
}
.price_block button{
    top: 520px;
    left: 46%;
    position: fixed;
    font-size: 18px;
}
.price_field {
    font-size: 20px;
    height: 50px;
    color: black;
    text-align: center;
    border: 1px solid aqua;
    background-color: transparent;
}

